.resume {
  .info-section {
    width: 20%;
  }

  .content-area {
    background: $color3;
    margin-left: 20%;
    width: 80%;
    min-width: 300px;
    background: white;
    height: 100%;
    // This is a trick of removing the padding from the .content-area selector in this .scss file and putting it inside the .scroll-section selector inside of the main.scss file.
    // padding: 56px;
    overflow: hidden;

    .category {
      margin-bottom: 56px;

      h1 {
        font-weight: 700;
        font-size: 2rem;
        text-transform: uppercase;
        margin-bottom: 56px;
      }

      .xp-section {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 56px;
        grid-row-gap: 30px;
        max-width: 1000px;

        .xp-box {
          padding: 30px;
          transition: all 0.5s ease-in-out;
          cursor: pointer;

          &:hover {
            -webkit-box-shadow: 0 20px 74px 6px rgba(0, 0, 0, 0.12);
            -moz-box-shadow: 0 20px 74px 6px rgba(0, 0, 0, 0.12);
            box-shadow: 0 20px 74px 6px rgba(0, 0, 0, 0.12);
          }

          h5 {
            font-weight: 500;
            font-size: 1.3rem;
            margin-bottom: 1.4rem;
          }

          p {
            font-weight: 300;
            font-size: 1rem;
            margin-bottom: 1rem;
          }
        }
      }
    }
  }
}

// Phone
@media (min-width: 0px) and (max-width: 767px) {
  .resume {
    .open-menu-icon {
      top: -3px;
      right: 20px;

      .fas {
        color: white;
      }
    }

    .content-area {
      height: auto;
      margin-left: 0;
      top: 70px;
      width: 100%;

      &:after {
        content: "";
        display: table;
        clear: both;
      }

      .category {
        width: 100%;

        .xp-section {
          grid-template-columns: 1fr;

          .xp-box {
            padding: 0;
          }
        }
      }
    }

    .info-section {
      padding: 10px 30px;
      position: static;

      .logo {
        height: 50px;
        width: 70px;
        font-size: 1rem;
      }

      .info-box {
        display: none;
      }
    }
  }
}

// Tablet
@media (min-width: 768px) and (max-width: 991px) {
  .resume {
    .info-section {
      width: 30%;
      padding: 56px 30px;

      .icon-section {
        a {
          font-size: 1.4rem;
        }
      }
    }

    .content-area {
      height: auto;
      width: 70%;
      margin-left: 30%;

      &:after {
        content: "";
        display: table;
        clear: both;
      }

      .category {
        width: 100%;

        .xp-section {
          grid-template-columns: 1fr;
        }
      }
    }
  }
}

// Laptop
@media (min-width: 992px) and (max-width: 1199px) {
  .resume {
    .info-section {
      width: 30%;

      .icon-section {
        a {
          font-size: 1.4rem;
        }
      }
    }

    .content-area {
      height: auto;
      width: 70%;
      margin-left: 30%;

      &:after {
        content: "";
        display: table;
        clear: both;
      }
    }
  }
}

// Extra Large
@media (min-width: 1200px) {
  .resume {
    .info-section {
      width: 30%;

      .icon-section {
        a {
          font-size: 1.4rem;
        }
      }
    }
  }
}
